<template>
  <v-container id="login" class="fill-height" fluid>
    <v-row align="center" justify="center">
      <v-col cols="12" sm="8" md="4">
        <v-card class="elevation-12">
          <v-toolbar color="indigo" dark flat>
            <v-toolbar-title>BMW | Make Ready Login</v-toolbar-title>
          </v-toolbar>
          <v-card-text class="text-center">
            <h1 class="heading">Welcome to BMW Make Ready</h1>
            <h3>Please use your Principle Auto Google account! This application will not work with your personal email address.</h3>
          </v-card-text>
          <v-row justify="center">
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn @click.prevent="signup" dark color="deep-orange darken-2">Login with Google</v-btn>
            </v-card-actions>
          </v-row>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { db } from "../main";
import firebase from "firebase";

export default {
  name: "Login",
  data() {
    return {};
  },
  methods: {
    signup() {
      const provider = new firebase.auth.GoogleAuthProvider();

      firebase
        .auth()
        .signInWithPopup(provider)
        .then(cred => {
          let ref = db.collection("users").doc(cred.user.email);
          ref.get().then(doc => {
            if (doc.exists) {
              ref.update({
                photo: cred.user.photoURL
              });
              this.$router.push("/");
            } else {
              console.log(cred);
              ref.set({
                display_name: cred.user.displayName,
                user_id: cred.user.uid,
                email: cred.user.email,
                access_level: "Unidentified"
              });
              this.$router.push("/unidentified");
            }
          });
        })
        .catch(err => {
          alert("Oops. " + err.message);
        });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.heading {
  margin-bottom: 10px;
}

button {
  margin-bottom: 10px;
}

.text-center {
  text-align: center;
  margin-top: 10px;
}
</style>



