<template>
  <v-container id="parts">
    <div class="display-3">Detail</div>
    <v-simple-table>
      <thead>
        <tr>
          <th class="text-left">#</th>
          <th class="text-left">Sales Associate</th>
          <th class="text-left">Customer Name</th>
          <th class="text-left">Type of Vehicle</th>
          <th class="text-left">Stock #</th>
          <th class="text-left">Year</th>
          <th class="text-left">Model</th>
          <th class="text-left"></th>
          <th class="text-left"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(req, i) in reqs" :key="i">
          <!-- Request number in queue -->
          <td>{{ i + 1 }}</td>
          <!-- Associates Name + Avatar -->
          <td>
            <v-avatar size="36">
              <img :src="photo" alt="user" />
            </v-avatar>
            {{ req.associate }}
          </td>
          <!-- Customer Name -->
          <td>{{ req.customer_name }}</td>
          <!-- Vehicle Type -->
          <td>{{ req.vehicle_type }}</td>
          <!-- Stock Number -->
          <td>{{ req.stock_number }}</td>
          <!-- Vehicle Year -->
          <td>{{ req.vehicle_year }}</td>
          <!-- Vehicle Model -->
          <td>{{ req.vehicle_model }}</td>
          <!-- Customer Care Package Options -->
          <td>
            <v-select
              v-if="req.detail_status == 'Claimed'"
              color="deep-orange darken-2"
              v-model="req.delivery_bay"
              :items="bays"
              label="Which delivery bay?"
            ></v-select>
          </td>
          <td>
            <v-btn
              v-if="req.detail_status == 'Currently Waiting'"
              @click="claimMakeReady(req)"
              color="success"
            >Claim</v-btn>
            <v-btn
              @click="completeMakeReady(req)"
              v-if="req.detail_status == 'Claimed'"
              color="success"
            >Complete</v-btn>
            <v-btn
              @click="unclaimMakeReady(req)"
              outlined
              v-if="req.detail_status == 'Claimed'"
              color="error"
            >Unclaim</v-btn>
          </td>
        </tr>
      </tbody>
    </v-simple-table>
  </v-container>
</template>

<script>
import { db } from "../main";
import firebase from "firebase";

export default {
  name: "Parts",
  data() {
    return {
      weowes: [],
      reqs: [],
      bays: ["Bay 1", "Bay 2", "Bay 3", "Bay 4", "Overflow"],
      status: ["Approved", "Denied"],
      claimed: ["Claim"],
      boolean: ["Yes", "No"],
      user: firebase.auth().currentUser.displayName,
      photo: firebase.auth().currentUser.photoURL
    };
  },
  firestore() {
    return {
      reqs: db
        .collection("makeReady")
        .where("detail_state", "==", true)
        .orderBy("make_ready_init_timestamp", "desc")
    };
  },
  methods: {
    weOweClaim(wo) {
      db.collection("weowe")
        .doc(wo.id)
        .update({
          parts_claimed_timestamp: new Date(),
          parts_associate: this.user,
          parts_status: "Claimed"
        });
    },
    weOweComplete(wo) {
      db.collection("weowe")
        .doc(wo.id)
        .update({
          parts_complete_timestamp: new Date(),
          parts_associate: this.user,
          parts_status: "Complete",
          parts_on_order: wo.parts_on_order,
          invoice_number: wo.invoice_number,
          parts_state: false
        });
    },
    claimMakeReady(req) {
      db.collection("makeReady")
        .doc(req.id)
        .update({
          detail_claimed_timestamp: new Date(),
          detail_associate: this.user,
          detail_status: "Claimed"
        });
    },
    unclaimMakeReady(req) {
      db.collection("makeReady")
        .doc(req.id)
        .update({
          detail_claimed_timestamp: "",
          detail_associate: "",
          detail_status: "Currently Waiting"
        });
    },
    completeMakeReady(req) {
      db.collection("makeReady")
        .doc(req.id)
        .update({
          detail_complete_timestamp: new Date(),
          detail_associate: this.user,
          detail_status: "Complete",
          detail_state: false,
          delivery_bay: req.delivery_bay
        });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
img {
  margin-right: 10px;
}

.display-3 {
  margin-bottom: 40px;
  margin-top: 20px;
}

.bottom {
  margin-top: 100px;
}
</style>



